import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import index from '../views/index.vue' //


import emergency_video from '@/views/emergencyCommand/video.vue' //
import vehicleList from '@/views/emergencyCommand/vehicleList.vue' //
import journal from '@/views/logbook/journal.vue' //
import drive from '@/views/assistedDriving/drive.vue' //
import system from '@/views/system/system.vue' // 

import groundEnd from '@/views/system/groundEnd.vue' // 
import notice from '@/views/system/notice.vue' // 
import typesetting from '@/views/system/typesetting.vue' //  
import electronics from '@/views/system/electronics.vue' // 

Vue.use(Router)
// 
const router = new Router({
  mode: 'hash', //url#
  routes: [
    {
      path: '/Login',
      name: 'Login',
      meta: {
        title: ''
      },
      component: () => import('@/views/Login.vue')
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '',
        requiresAuth: true, // true
      },
      children: [
        {
          path: "/emergency_video",
          component: emergency_video,
          hidden: true
        },
        {
          path: "/vehicleList",
          component: vehicleList,
          hidden: true
        },
        {
          path: "/index",
          component: index,
          hidden: true
        },
        {
          path: "/journal",
          component: journal,
          hidden: true,
          
        },
        
        {
          path: "/drive",
          component: drive,
          hidden: true
        },
        {
          path: "/system",
          component: system,
          hidden: true
        },
        {
          path: "/groundEnd",
          component: groundEnd,
          hidden: true
        },
        {
          path: "/notice",
          component: notice,
          hidden: true
        },
        {
          path: "/typesetting",
          component: typesetting,
          hidden: true
        },
        {
          path: "/electronics",
          component: electronics,
          hidden: true
        },

      ]
    },

  ]
})

// 
router.beforeEach((to, from, next) => {
   let user = JSON.parse(localStorage.getItem("user")) ? JSON.parse(localStorage.getItem("user")) : {};
   console.log(user,'aaa')
   // return
   // if (to.path == "Login") {
   //  next()
   // }else {
    if(to.name!=='Login'){
     if(user && user.token) {
      next()
     }else {
      next('/Login')
     }
    }else{
     next()
    }
    
   // }
  })

export default router
